import React,{useEffect} from 'react';
import ShowHelpContent from "../ShowHelpContent";
import {updateAppSettings} from "src/actions/foundationActions";
import store from 'src/store';
import {updateTesting} from "src/actions/testingActions";

const helpText = '';

const ShowHelpContentTest = (props) => {
    useEffect(()=>{
        store.dispatch(updateAppSettings({helpText:helpText}))
        store.dispatch(updateTesting({helpText:helpText}))
    },[]);
    return (
        <div>
            <div>Show Help</div>
            <ShowHelpContent/>
        </div>
    )
};

export default ShowHelpContentTest;
